<template>
  <div class="container">
    <div class="flex">
      <div class="txt">姓名</div>
      <div>{{info.name}}</div>
    </div>
    <div class="flex">
      <div class="txt">手机号</div>
      <div>{{info.mobile}}</div>
    </div>
    <div class="flex">
      <div class="txt">部门</div>
      <div>{{info.department}}</div>
    </div>
    <div class="flex">
      <div class="txt">公司</div>
      <div>{{info.company}}</div>
    </div>
    <div class="flex">
      <div class="txt">员工编号</div>
      <div>{{info.no}}</div>
    </div>
    <div class="submit" @click="logout">退出登录</div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data () {
    return {
    }
  },

  components: {},

  computed: {
    ...mapGetters({
      info: 'info'
    })
  },

  mounted: function() {
     let a = '123456789';
    let b = a.substr(1,-1);
    console.log(a,b)
  },

  methods: {
    logout() {
      this.$store.dispatch('FedLogOut').then(() => {
        location.reload()
      })
    }
  }
}
</script>

<style lang='scss' scoped>
  .container {
    width: 400px;
    padding: 40px;
    .flex {
      padding: 10px;
      border-bottom: 1px solid #999;
      margin-bottom: 10px;
    }
    .submit {
      margin-top: 50px;
      background: #2B4365;
      text-align: center;
      color: #fff;
      line-height: 40px;
      border-radius: 10px;
      cursor: pointer;
    }
  }
</style>



